import { limit } from '../../constants/const.js'
const db = wx.cloud.database()
import { transQuestion } from '../../utils/transData.js'
export default {
	data() {
		return {
			list: [],
			total: 0,
			skip: 0
		};
	},

	methods: {
		radioChange(e, i) {
			let { value } = e.detail
			this.answers[i].answer = value
		},
		
		async getTotal() {
			let res = await db.collection('questions').count()
			this.total = res.total
		},
		
		async getList() {
			try{
				let res = await db.collection('questions').skip(this.skip*10).limit(limit).get()
				if (res.data.length) {
					this.list = [...this.list, ...transQuestion(res.data)]
				}
				
				
			}catch(e){
				console.log(e)
			}
		}
	},
	onReachBottom() {
		this.skip += 1
		if (this.list.length < this.total) {
			this.getList()
		}
		
	},
	onLoad() {
		this.getTotal()
		this.getList()
	}
}
